import React, {FunctionComponent} from "react";


interface HeadingsProps {
    className?: string
    title: string
    showTitle: boolean
    page?: string | null
    byline?: string | null
}

export const Headings: FunctionComponent<HeadingsProps> = ({
    className = "",
    title,
    showTitle = true,
    page,
    byline = null
}) => {

    const titleVisibilityClass = showTitle ? "visible" : "invisible"
    const bylineVisibilityClass = byline ? "visible" : "invisible"

    return <div className={`${className} grid grid-cols-4 flex-row justify-end`}>
        <div className="col-span-1"></div>
        <div className="col-span-3 flex flex-col text-right gap-2">
            <h1 className={titleVisibilityClass}>{title}</h1>
            <h2>{page}</h2>
            <h3 className={`${bylineVisibilityClass}`}>{byline || "nbsp;"}</h3>
        </div>
    </div>
}