import React, {FunctionComponent, PropsWithChildren} from "react";
import {Headings} from "../components/Headings";
import {quotes} from "../quotes";
import _ from "lodash";
import {Tooltip, TooltipContent, TooltipProvider, TooltipTrigger} from "../components/ui/tooltip";
import {Link} from "gatsby";

interface MainLayoutProps {
    className?: string | null
    title: string
    showTitle: boolean
    pageTitle?: string | null
    byline?: string | null
    version: string
}

export const MainLayout: FunctionComponent<MainLayoutProps & PropsWithChildren> = ({
    className = "",
    children,
    title,
    showTitle,
    pageTitle,
    byline,
    version,
}) => {

    const quote = _.shuffle(quotes)[0];

    return <TooltipProvider>
        <div className={"min-h-max grid grid-flow-row gap-0"}>
            <div className="row-span-1">
                <header className="bg-secondary pt-72">
                    <div className="container mx-auto">
                        <Headings
                            className={"pb-5"}
                            title={title}
                            showTitle={showTitle}
                            page={pageTitle}
                            byline={byline}
                        />
                    </div>
                </header>
                <main className="">
                    <div className="container mx-auto grid grid-cols-12 gap-3 pt-10">
                        <div className="col-span-4 h-full grid grid-rows-2">

                        </div>
                        <div className={`col-span-8 ${className}`}>
                            {children}
                        </div>
                    </div>
                </main>
            </div>
            <div className="row-span-1 container mx-auto mt-7 mb-7 flex">
                <div className="grid w-full grid-cols-3">
                    <div className="col-span-1 flex flex-col justify-end">
                        <div className="flex flex-col justify-end">
                            <span>{version}</span>
                        </div>
                    </div>
                    <div className="col-span-2 flex flex-col justify-end">
                        <Tooltip delayDuration={0}>
                            <TooltipTrigger className="items-end justify-end text-right">
                                {
                                    quote.link
                                        ? <Link to={quote.link} className="italic w-auto">"{quote.quote}"</Link>
                                        : <span>{quote.quote}</span>
                                }
                            </TooltipTrigger>
                            <TooltipContent>
                                {quote.source}
                            </TooltipContent>
                        </Tooltip>
                    </div>
                </div>
            </div>
        </div>
    </TooltipProvider>
}